 :root {
	--base-fz : 16px;
	--base-tc : #756B6B;
	--base-pl : #756B6B;
	--base-bg : #F4F2F2;

	--pr:   #EE665F;
	--pr-h: #EE6042;
	--gray: #756B6B;
	--gray-light: #EDEAEA;
	--wh: #fff;
	--bl: #000; }

*, ::before, ::after {
	box-sizing: border-box; }

body {
	font-family: 'Averta';
	font-size: var(--base-fz);
	line-height: 1.52;
	color: var(--bl);
	font-weight: 400;
	background-color: var(--base-bg); }

.logo {
	position: relative;
	cursor: pointer;
	& &__part {
		--w: 9rem;
		--h: 1.5rem;
		color: var(--bl); }
	& &__detail {
		--w: 10.33px;
		--h: 15.06px;
		top: 0;
		left: 4.5rem;
		position: absolute; } }


svg {
	--w: 1.5rem;
	--h: 1.5rem;
	position: relative;
	width: var(--w);
	height: var(--h); }

a,
button,
.cur-p,
.btn, {
	cursor: pointer;
	text-decoration: none;
	transition: .25s; }


a,
a:focus,
a:focus-within,
a:active,
a:hover,
button,
button:focus,
button:focus-within,
.btn,
.btn:hover,
.btn:focus,
.btn:active {
	outline: none !important;
	border: none; }

a {
	color: var(--pr);
	&:active {
		color: var(--pr); }
	&:hover {
		color: var(--pr-h);
		text-decoration: none; } }

.ov-hidden {
	overflow: hidden; }

.relative {
	position: relative; }

.d-flex {
	display: flex; }

.d-justify {
	justify-content: center; }

.d-justify-between {
	justify-content: space-between; }

.tt-uppercase {
	text-transform: uppercase; }

.animate::before,
.animate::after,
.animate {
	transition: all .25s liner; }

.text-limit span,
.text-limit {
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	display: inline-block;
	max-width: 100%; }

.form-control::-webkit-input-placeholder,
.form-control::-moz-placeholder,
.form-control:-ms-input-placeholder,
.form-control::placeholder {
	color: $default-placeholder-color; }

.after,
.before {
	position: relative; }

.after::after,
.before::before {
	content: '';
	position: absolute; }


.italic {
	font-style: italic; }

$fwList: 300 400 500 600 700;
@each $fontWeight in $fwList {
	.fw-#{$fontWeight} {
		font-weight: $fontWeight; } }

$fzList: 14px 16px 18px;
@each $fontSize in $fzList {
	.fz-#{$fontSize} {
		font-size: $fontSize; } }

.mb-20 {
	margin-bottom: 20px; }

.block-divider {
	--p: 0;
	--m: 0;
	margin: var(--m);
	padding: var(--p); }


.text-mutted {
	font-size: 1rem;
	color: var(--gray); }


.grid {
	--jc: space-between;
	--ai: flex-start;
	--intval: 20px;
	--fxw: nowrap;
	--card-min: auto;
	--card-max: auto;
	display: flex;
	justify-content: var(--jc);
	align-items: var(--ai);
	margin: 0 calc(var(--intval) * -1);
	flex-wrap: var(--fxw);
	.grid-item {
		padding: 0 var(--intval);
		min-width: var(--card-min);
		max-width: var(--card-max); } }


.detail {
	--sz: 31.25rem;
	--c: #EE665F;
	--t : initial;
	--b : initial;
	--r : initial;
	--l : initial;
	--rt: 0deg;
	position: absolute;
	width: var(--sz);
	height: var(--sz);
	color: var(--c);
	top: var(--t);
	bottom: var(--b);
	right: var(--r);
	left: var(--l);
	transform: rotate(var(--rt)); }
