.container {
	--w: 1330px;
	--p: 30px;
	max-width: var(--w);
	padding-left: var(--p);
	padding-right: var(--p);
	margin-right: auto;
	margin-left: auto;
	position: relative;
	&-fluid {
		--w: 100%;
		--p: 80px; } }


.running-line {
	--mt: -86px;
	margin-top: var(--mt);
	z-index: 9;
	position: relative;
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	& &__box {
		--fz: 140px;
		--time: 20s;
		font-size: var(--fz);
		color: #212121;
		display: inline-block;
		padding-left: 100vw;
		animation: runningText var(--time) infinite linear;
		white-space: nowrap;
		position: relative;
		&--transparent {
			color: transparent;
			.text-line {
				position: absolute;
				top: 0;
				left: 100vw;
				letter-spacing: 0.2375rem; } } } }

@keyframes runningText {
	0% {
		transform: translate(0, 0); }
	100% {
		transform: translate(-100%, 0); } }


.text-line {
	--w: 100%;
	--h: 100%;
	--color: #000000;
	--border: 2px;
	--fw: 700;
	--fz: 8.75rem;
	width: var(--w);
	height: var(--h);
	stroke: var(--color);
	stroke-width: var(--border);
	font-weight: var(--fw);
	font-size: var(--fz);
	display: flex;
	&-with-ghost {
		position: relative;
		color: transparent;
		--fz-ghost: 30px;
		font-size: var(--fz-ghost);
		.text-line {
			--t: 0;
			--l: 0;
			position: absolute;
			top: var(--t);
			left: var(--l);
			font-size: var(--fz-ghost); } } }


@keyframes dash {
	to {
		stroke-dashoffset: 0; } }


@keyframes filling {
	from {
		fill: #2A4A73;
		fill-opacity: 0; }

	to {
		fill: #2A4A73;
		fill-opacity: 1; } }




.icon-near-text {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: flex-start;
	&__icon {
		margin-right: 2.375rem;
		.icon {
			--s: 50px;
			width: var(--s);
			height: var(--s); } }
	&__wrap {}

	&__title {
		font-weight: 600;
		font-size: 1.5rem;
		margin: 0.3125rem 0 2.125rem;
		line-height: 1.4; }
	&__desc {
		color: var(--gray);
		font-weight: 400;
		line-height: 1.6; } }





.header {
	background-color: var(--gray-light);
	min-height: 67.5rem; }



.header-slide {
	padding-top: 9.125rem;
	&__meta {
		margin: 0; }
	&__title {
		font-size: 6.5625rem;
		margin: 0;
		line-height: 1.25;
		margin-bottom: 2.625rem;
		font-weight: 400;
		letter-spacing: 0.09375rem; }
	&__btn {} }




.block-title {
	font-size: 5.75rem;
	font-weight: 700;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: flex-start;
	& &__icon {
		line-height: 1;
		.text-line {
			--border: 0.0625rem;
			--color: var(--pr);
			--fz: 2.25rem; } }
	& &__text {
		margin-left: -0.25rem;
		letter-spacing: -0.0625rem;
		&-line {
			--fz-ghost: 5.75rem;
			margin-top: -0.125rem;
			margin-left: -0.3125rem;
			letter-spacing: -0.0625rem;
			.text-line {
				--border: 1px; } } } }






.partners {
	& &__box {
		justify-content: space-between; }
	& &__text {}
	& &__btn-holder {
		align-self: flex-end;
		margin-bottom: 2.25rem; } }




.services {
	& &__wrapper {
		margin-top: 11.625rem; }
	& &__item {
		margin: 5.9375rem 0;
		&-title {
			font-size: 4.375rem;
			margin: 0;
			cursor: pointer;
			&.text-mutted {
				font-size: 2rem;
				font-weight: 400; } } } }



.quotation {
	background-color: var(--gray-light);
	min-height: 1080px;
	.container {
		padding-top: 15rem; }
	& &__box {
		position: relative;
		padding: 8.4375rem 0 0;
		margin-left: 10.25rem;
		.icon-quote {
			--w: 7.875rem;
			--h: 7.875rem;
			position: absolute;
			top: 0;
			left: 0;
			color: var(--pr); }
		.icon-hero-element {
			--w: 9rem;
			--h: 9rem;
			position: absolute;
			bottom: -7.1875rem;
			right: -7.8125rem; } }
	& &__quote {
		margin: 0 0 1.5rem 10.5rem;
		&-text {
			font-size: 2.5rem;
			font-weight: 600;
			margin: 0; } }
	& &__caption {
		display: flex;
		flex-direction: column;
		line-height: 1.65;
		&-top {
			font-size: 1.125rem;
			font-weight: normal;
			font-weight: 700; }
		&-bottom {
			font-size: 0.875rem;
			color: var(--gray);
			font-weight: 400; } }
	.detail {
		&.detail-1 {
			--t: -6.5rem;
			--r: -27.5rem;
			--sz: 29.8125rem;
			--rt: -2deg; }
		&.detail-2 {
			--b: -15.625rem;
			--l: -31.625rem;
			--sz: 35.625rem;
			--rt: 87deg; } } }



